"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { createClient } from "@/lib/supabase/client"
import { ArrowLeft, Plus, Edit2, Trash2, QrCode, Users } from "lucide-react"

type TableStatus = 'available' | 'occupied' | 'reserved'

export default function TablesPage() {
  const router = useRouter()
  const supabase = createClient()
  const [loading, setLoading] = useState(true)
  const [tables, setTables] = useState<any[]>([])
  const [showModal, setShowModal] = useState(false)
  const [editingTable, setEditingTable] = useState<any>(null)
  const [restaurant, setRestaurant] = useState<any>(null)
  const [showQRModal, setShowQRModal] = useState(false)
  const [selectedQRTable, setSelectedQRTable] = useState<any>(null)
  
  const [formData, setFormData] = useState({
    table_number: '',
    capacity: '',
    status: 'available' as TableStatus
  })

  useEffect(() => {
    loadTables()
  }, [])

  async function loadTables() {
    setLoading(true)
    try {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) {
        router.push('/login')
        return
      }

      // Get selected restaurant from localStorage
      const selectedRestaurantId = localStorage.getItem('selected_restaurant_id')
      if (!selectedRestaurantId) {
        router.push('/dashboard')
        return
      }

      const { data: restaurants } = await supabase
        .from('restaurants')
        .select('*')
        .eq('id', selectedRestaurantId)
        .eq('owner_id', user.id)
        .single()

      if (!restaurants) return
      setRestaurant(restaurants)

      const { data: tablesData } = await supabase
        .from('tables')
        .select('*')
        .eq('restaurant_id', restaurants.id)
        .order('table_number')

      if (tablesData) {
        setTables(tablesData)
      }
    } catch (error) {
      console.error('Error loading tables:', error)
    } finally {
      setLoading(false)
    }
  }

  function openModal(table: any = null) {
    if (table) {
      setEditingTable(table)
      setFormData({
        table_number: table.table_number || '',
        capacity: table.capacity?.toString() || '',
        status: table.status || 'available'
      })
    } else {
      setEditingTable(null)
      setFormData({
        table_number: '',
        capacity: '',
        status: 'available'
      })
    }
    setShowModal(true)
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault()
    
    if (!restaurant) return

    const tableData = {
      table_number: formData.table_number,
      capacity: parseInt(formData.capacity),
      status: formData.status,
      restaurant_id: restaurant.id
    }

    try {
      if (editingTable) {
        await supabase
          .from('tables')
          .update(tableData)
          .eq('id', editingTable.id)
      } else {
        await supabase
          .from('tables')
          .insert(tableData)
      }

      setShowModal(false)
      loadTables()
    } catch (error) {
      console.error('Error saving table:', error)
    }
  }

  async function deleteTable(id: string) {
    if (!confirm('Hapus meja ini?')) return

    try {
      await supabase
        .from('tables')
        .delete()
        .eq('id', id)

      loadTables()
    } catch (error) {
      console.error('Error deleting table:', error)
    }
  }

  async function updateTableStatus(id: string, status: TableStatus) {
    try {
      await supabase
        .from('tables')
        .update({ status })
        .eq('id', id)

      loadTables()
    } catch (error) {
      console.error('Error updating table status:', error)
    }
  }

  function showQRCode(table: any) {
    setSelectedQRTable(table)
    setShowQRModal(true)
  }

  const getStatusColor = (status: TableStatus) => {
    switch (status) {
      case 'available': return 'bg-green-100 text-green-800 border-green-200'
      case 'occupied': return 'bg-red-100 text-red-800 border-red-200'
      case 'reserved': return 'bg-yellow-100 text-yellow-800 border-yellow-200'
    }
  }

  const getStatusText = (status: TableStatus) => {
    switch (status) {
      case 'available': return 'Tersedia'
      case 'occupied': return 'Terisi'
      case 'reserved': return 'Dipesan'
    }
  }

  const availableTables = tables.filter(t => t.status === 'available').length
  const occupiedTables = tables.filter(t => t.status === 'occupied').length

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[#FF5A5F]"></div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white border-b border-gray-200 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <button
                onClick={() => router.back()}
                className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <ArrowLeft className="w-5 h-5" />
              </button>
              <h1 className="text-2xl font-bold text-gray-900">Kelola Meja</h1>
            </div>
            <button
              onClick={() => openModal()}
              className="flex items-center gap-2 px-4 py-2 bg-[#FF5A5F] text-white rounded-lg font-medium hover:bg-[#FF5A5F]/90 transition-colors"
            >
              <Plus className="w-5 h-5" />
              Tambah Meja
            </button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Table Stats */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm">
            <h3 className="text-3xl font-bold text-gray-900 mb-1">{tables.length}</h3>
            <p className="text-sm text-gray-600">Total Meja</p>
          </div>
          <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm">
            <h3 className="text-3xl font-bold text-green-600 mb-1">{availableTables}</h3>
            <p className="text-sm text-gray-600">Tersedia</p>
          </div>
          <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm">
            <h3 className="text-3xl font-bold text-red-600 mb-1">{occupiedTables}</h3>
            <p className="text-sm text-gray-600">Terisi</p>
          </div>
        </div>

        {/* Tables Grid */}
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
          {tables.length === 0 ? (
            <div className="col-span-full bg-white rounded-2xl p-12 text-center border border-gray-100">
              <p className="text-gray-500 text-lg">Belum ada meja</p>
            </div>
          ) : (
            tables.map((table) => (
              <div
                key={table.id}
                className={`bg-white rounded-2xl p-6 border-2 ${getStatusColor(table.status)} shadow-sm hover:shadow-md transition-all`}
              >
                <div className="text-center mb-4">
                  <h3 className="text-3xl font-bold text-gray-900 mb-2">
                    {table.table_number}
                  </h3>
                  <div className="flex items-center justify-center gap-2 text-gray-600 mb-2">
                    <Users className="w-4 h-4" />
                    <span className="text-sm">{table.capacity} kursi</span>
                  </div>
                  <span className={`inline-block px-3 py-1 rounded-full text-xs font-semibold ${getStatusColor(table.status)}`}>
                    {getStatusText(table.status)}
                  </span>
                </div>

                {/* Status Quick Actions */}
                <div className="grid grid-cols-3 gap-1 mb-3">
                  <button
                    onClick={() => updateTableStatus(table.id, 'available')}
                    className={`p-2 text-xs rounded ${table.status === 'available' ? 'bg-green-500 text-white' : 'bg-gray-100 text-gray-600'}`}
                    title="Tersedia"
                  >
                    ✓
                  </button>
                  <button
                    onClick={() => updateTableStatus(table.id, 'occupied')}
                    className={`p-2 text-xs rounded ${table.status === 'occupied' ? 'bg-red-500 text-white' : 'bg-gray-100 text-gray-600'}`}
                    title="Terisi"
                  >
                    ●
                  </button>
                  <button
                    onClick={() => updateTableStatus(table.id, 'reserved')}
                    className={`p-2 text-xs rounded ${table.status === 'reserved' ? 'bg-yellow-500 text-white' : 'bg-gray-100 text-gray-600'}`}
                    title="Dipesan"
                  >
                    ◐
                  </button>
                </div>

                {/* Actions */}
                <div className="flex gap-2">
                  <button
                    onClick={() => showQRCode(table)}
                    className="flex-1 flex items-center justify-center gap-1 px-2 py-2 bg-[#8B5CF6] text-white rounded-lg text-xs font-medium hover:bg-[#8B5CF6]/90 transition-colors"
                  >
                    <QrCode className="w-3 h-3" />
                    QR
                  </button>
                  <button
                    onClick={() => openModal(table)}
                    className="px-2 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
                  >
                    <Edit2 className="w-3 h-3" />
                  </button>
                  <button
                    onClick={() => deleteTable(table.id)}
                    className="px-2 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors"
                  >
                    <Trash2 className="w-3 h-3" />
                  </button>
                </div>
              </div>
            ))
          )}
        </div>
      </main>

      {/* Table Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-2xl p-6 max-w-md w-full">
            <h2 className="text-2xl font-bold mb-6">
              {editingTable ? 'Edit Meja' : 'Tambah Meja'}
            </h2>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Nomor Meja
                </label>
                <input
                  type="text"
                  required
                  value={formData.table_number}
                  onChange={(e) => setFormData({...formData, table_number: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                  placeholder="Contoh: A1, B2, VIP-1"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Kapasitas (Kursi)
                </label>
                <input
                  type="number"
                  min="1"
                  required
                  value={formData.capacity}
                  onChange={(e) => setFormData({...formData, capacity: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Status
                </label>
                <select
                  value={formData.status}
                  onChange={(e) => setFormData({...formData, status: e.target.value as TableStatus})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                >
                  <option value="available">Tersedia</option>
                  <option value="occupied">Terisi</option>
                  <option value="reserved">Dipesan</option>
                </select>
              </div>
              <div className="flex gap-2 pt-4">
                <button
                  type="button"
                  onClick={() => setShowModal(false)}
                  className="flex-1 px-4 py-2 border border-gray-200 text-gray-700 rounded-lg font-medium hover:bg-gray-50 transition-colors"
                >
                  Batal
                </button>
                <button
                  type="submit"
                  className="flex-1 px-4 py-2 bg-[#FF5A5F] text-white rounded-lg font-medium hover:bg-[#FF5A5F]/90 transition-colors"
                >
                  {editingTable ? 'Simpan' : 'Tambah'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* QR Code Modal */}
      {showQRModal && selectedQRTable && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-2xl p-6 max-w-md w-full text-center">
            <h2 className="text-2xl font-bold mb-4">QR Code Meja {selectedQRTable.table_number}</h2>
            <div className="bg-gray-100 p-8 rounded-xl mb-4 flex items-center justify-center">
              <div className="w-64 h-64 bg-white p-4 rounded-lg shadow-lg flex items-center justify-center">
                <QrCode className="w-32 h-32 text-gray-400" />
              </div>
            </div>
            <p className="text-sm text-gray-600 mb-6">
              Scan QR code ini untuk melihat menu dan memesan
            </p>
            <div className="flex gap-2">
              <button
                onClick={() => setShowQRModal(false)}
                className="flex-1 px-4 py-2 border border-gray-200 text-gray-700 rounded-lg font-medium hover:bg-gray-50 transition-colors"
              >
                Tutup
              </button>
              <button
                onClick={() => window.print()}
                className="flex-1 px-4 py-2 bg-[#FF5A5F] text-white rounded-lg font-medium hover:bg-[#FF5A5F]/90 transition-colors"
              >
                Print QR
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
